import React from 'react';
import ReactDOM from 'react-dom'

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'


import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import Paper from '@material-ui/core/Paper'
import Hidden from '@material-ui/core/Hidden'

import Divider from '@material-ui/core/Divider';


import ProbGraph from '../components/ProbGraph.jsx'

import ScrollspyNav from "react-scrollspy-nav";

import '../App.css';

import {large_threshold} from '../files/constants.js'
import {locationNumberToName, lifeExpectancy, genMortalityProb} from '../files/constants.js'

import {
  VictoryBar,
  VictoryChart,
  VictoryAxis,
  VictoryStack,
  VictoryTheme,
  VictoryLabel
} from 'victory';

//Image imports



import coiling_img from '../images/coiling.svg'
import donothing_img from '../images/donothing.png'
import angio_img from '../images/angio.jpg'

import pron_file from '../files/ca_pronounced.mp3'


import ChevronRight from '@material-ui/icons/ChevronRight'


//
var pron = new Audio(pron_file)

class Decision extends React.Component {

  updatePatientWithParams() {
    if (typeof this.props.match.params.age !== 'undefined') {
      var newPatient = this.props.patient;
      newPatient.sex = this.props.match.params.sex;
      newPatient.age = this.props.match.params.age;
      newPatient.lesionDiameter = this.props.match.params.size;
      newPatient.lesionLocation = this.props.match.params.location;
      newPatient.ethnicity = this.props.match.params.ethnicity;
      newPatient.htn = this.props.match.params.htn === 'true'
      newPatient.hxSAH = this.props.match.params.sah === 'true'
      this.props.updatePatient(newPatient);
    }
  }
  constructor(props) {

    super(props);
    this.state = {
      isLarge: window.innerWidth > large_threshold,
      location: this.props.match.params.location !== 'undefined'
        ? locationNumberToName[this.props.match.params.location]
        : '',
      drawer: false,
      graphProb: 'isuia'
    }

    this.updatePatientWithParams();

  }
  componentWillMount() {
    window.scrollTo(0, 0);

    window.addEventListener('resize', () => {
      this.setState({
        isLarge: window.innerWidth > large_threshold
      });
    }, false);

  }

  componentDidUpdate() {
    var hash = this.props.location.hash.replace('#', '');
    if (hash) {
      let node = ReactDOM.findDOMNode(this.refs[hash]);
      if (node) {
        node.scrollIntoView()
        window.scrollBy(0, -100);
      }
    }
  }

  pronounce() {
    pron.play();
  }

  locationStatement() {
    var div = null;
    if (typeof this.state.location !== 'undefined') {
      div = " Your aneurysm is located on the " + locationNumberToName[this.props.match.params.location] + ".";
    }
    return div;
  }

  toggleDrawer() {
    this.setState({
      drawer: !this.state.drawer
    });
  };

  drawer() {
    return (<div className={'landing-nav norm_padding'}>
      <Typography variant={'h6'}>
        Quick Navigation:</Typography>
      <div className={'landing-nav'}>
        <ScrollspyNav scrollTargetIds={["risk", "decisionframe", "options"]} activeNavClass="is-active" router={'Router'}>
          <a href="#risk">Am I at risk?</a>

          <a href="#options">What are the treatment options?</a>
                    <a href="#decisionframe">Should I do something?</a>
        </ScrollspyNav>
      </div>
    </div>)
  }

  setGraphProb(study) {
    this.setState({graphProb: study});
  }

  lifetimeRisk(annualRuptureRisk) {
    var remaining = lifeExpectancy[this.props.patient.sex][this.props.patient.age];

    return (((remaining) * (1 - (1 - annualRuptureRisk / 100))) * 100).toFixed(2);
  }

  compareChart() {

    var valMon = parseInt(this.lifetimeRisk(this.props.patient[this.state.graphProb]/5));
    if(valMon > 100) { valMon=100}
    var valClip = parseInt(this.props.patient.isuiaSurg);;
    var valCoil = parseInt(this.props.patient.isuiaEndo);

    var dataBad = [
      {
        Choice: 'Monitor',
        Risk: valMon
      }, {
        Choice: 'Coiling',
        Risk: valCoil
      }, {
        Choice: 'Clipping',
        Risk: valClip
      }
    ];
    var dataGood = [
      {
        Choice: 'Monitor',
        Risk: 100 - valMon
      }, {
        Choice: 'Coiling',
        Risk: 100 - valCoil
      }, {
        Choice: 'Clipping',
        Risk: 100 - valClip
      }
    ]

    return (<VictoryChart domainPadding={20} theme={VictoryTheme.material}>
      <VictoryAxis tickValues={[1, 2, 3]} tickFormat={["Monitor", "Coiling", "Clipping"]}/>
      <VictoryAxis dependentAxis={true} axisLabelComponent={<VictoryLabel dy = {
          -30
        } />} label={'Risk (%)'} tickFormat={(x) => `${x}%`}/>
      <VictoryStack>
        <VictoryBar data={dataBad} x={'Choice'} y={'Risk'} style={{
            data: {
              fill: "#be202e"
            }
          }}/>
        <VictoryBar data={dataGood} x={'Choice'} y={'Risk'} style={{
            data: {
              fill: "#777777"
            }
          }}/>
      </VictoryStack>
    </VictoryChart >);
  }

  render() {
    var size_class = this.state.isLarge
      ? 'large'
      : '';
    return (<div>
      <Grid container={true} direction={'row'} alignContent={'stretch'}>
        <Grid container={true} item={true} justify={'flex-start'} alignContent={'flex-start'} className={'first-block'} md={9} xs={12}>
          <Paper>
            <Grid item={true} xs={12}>
              <Hidden mdUp={true}>
                {this.drawer()}
                <Divider/>
              </Hidden>
            </Grid>
            <Grid id={'risk'} item={true} container={true} xs={12} className={'content-block '} alignItems={'center'}>
              {
                this.props.patient.phases !== undefined
                  ?
                  <> <Grid item={true} container={true} direction={'column'} xs={12} spacing={16}></Grid>
                      <Grid item={true} xs={12} container={true} direction={size_class === 'large'
                          ? 'row'
                          : 'column'} >
                        <Grid container={true} item={true} xs={12} sm={6} md={8} direction={'column'} alignItems={'flex-start'} justify={'center'}>
                          <Grid item={true} container={true}>
                            <Typography variant={'h5'}>My Risk of Rupture in Next 5 Years:</Typography>
                            <Typography variant={'body1'} paragraph={true}>These data are from the major studies on unruptured aneurysms. Click on a number to see it in the graph on the right.</Typography>
                            {
                              this.props.patient.age <= 75
                                ? <Typography variant={'body1'} paragraph={true}>The small gray numbers show the calculated chance that a person with this level of risk would have a rupture in their whole life based on average life expectancy in the USA.</Typography>
                                : null
                            }
                            <Grid item={true} container={true} spacing={16} direction={'row'} xs={12} justify={'center'}>
                              {this.props.patient.ucas !== null ?
                              <Grid item={true} container={true} direction={'column'} alignItems={'center'} xs={4} onClick={() => this.setGraphProb('ucas')}>
                                <p className={'supertitle'}>UCAS</p>
                                <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.props.patient.ucas}%</Typography>
                                {
                                  this.props.patient.age <= 75
                                    ? <Typography variant={'body1'} justify={'center'} color={'secondary'} className={'lifetime-mortality'}>{this.lifetimeRisk(this.props.patient.ucas / 5)}%</Typography>
                                    : null
                                }
                              </Grid>: null}
                              <Grid item={true} container={true} direction={'column'} alignItems={'center'} xs={4} onClick={() => this.setGraphProb('isuia')}>
                                <p className={'supertitle'}>ISUIA</p>
                                <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.props.patient.isuia}%</Typography>
                                {
                                  this.props.patient.age <= 75
                                    ? <Typography variant={'body1'} justify={'center'} color={'secondary'} className={'lifetime-mortality'}>{this.lifetimeRisk( this.props.patient.isuia / 5)}%</Typography>
                                    : null
                                }
                              </Grid>
                              <Grid item={true} container={true} direction={'column'} alignItems={'center'} xs={4} onClick={() => this.setGraphProb('phases')}>
                                <p className={'supertitle'}>PHASES</p>
                                <Typography variant={'h3'} justify={'center'} color={'primary'} className={'big-number'}>{this.props.patient.phases}%</Typography>
                                {
                                  this.props.patient.age <= 75
                                    ? <Typography variant={'body1'} justify={'center'} color={'secondary'} className={'lifetime-mortality'}>{this.lifetimeRisk( this.props.patient.phases / 5)}%</Typography>
                                    : null
                                }
                              </Grid>
                            </Grid>

                          </Grid>
                        </Grid>
                        <Grid item={true} xs={12} sm={6} md={4} container={true} direction={'column'} alignItems={'center'} justify={'center'} >
                          <Typography variant={'body1'}>
                            <b>Graph of Risk from <span className={'all-caps'}>{this.state.graphProb}</span> Study</b>
                          </Typography>
                          <ProbGraph prob={this.props.patient[this.state.graphProb]}/>
                        </Grid>
                      </Grid>
                      <Grid item={true} xs={12} container={true} justify={'center'} alignContent={'center'} style={{'paddingTop':'30px'}}>
                        <Typography variant={'body1'} className={'callout'}>This range is an <b>estimate</b> for the average patient like you. The numbers come from 3 large studies of patients with aneurysms. Your medical provider will help you understand how your individual risk relates to these estimates.</Typography>
                      </Grid>
                    </>
                  : <div>
                      <Typography variant={'h6'} gutterBottom={true}>What is my risk of rupture?</Typography>
                      <Typography variant={'body1'} gutterBottom={true} paragraph={true}>There are 3 large studies of aneurysms that are used to predict how likely an aneurysm is to rupture. If you know details about your aneurysm, you can use the calculator below to see an estimate of your individual risk. Please note, that the risk may be more or less as accurate for certain patients depending on how many patients like you were in the study. our actual risk of rupture may be higher or lower based on other parts of your health history or characteristics of your aneurysm.</Typography>
                      <Button fullWidth={true} variant={'outlined'} color={'primary'} href="#/data-entry">Risk Calculator</Button>
                    </div>
              }

            </Grid>
            <Divider/>
            <Grid id={'options'} item={true} container={true} className={'content-block'}>
                <Typography variant={'h5'} gutterBottom={true}>
                  What are my options?
                </Typography>
                <Typography variant={'body1'} className={'limit_lg'} gutterBottom={true} paragraph={true}>The best option for you will depend on how likely it is that your aneurysm will rupture balanced against your individual needs and the risks of surgery. In general, there are three categories of treatment:
                  <b>rupture risk reduction, watch and wait, and surgery.</b>
                </Typography>
            </Grid>
            <Divider/>
            <Grid item={true} container={true} xs={12} className={'content-block'}direction={size_class === 'large'
                  ? 'row'
                  : 'column-reverse'}>
                <Grid item={true} container={true} xs={12} sm={8} direction={'column'} justify={'center'} alignItems={'flex-start'} spacing={16}>
                  <Grid item={true} className='landing_text_block'>
                    <Typography gutterBottom={true} variant="h6" component="h2">
                      For all patients: Rupture Risk Reduction
                    </Typography>
                    <Typography gutterBottom={true} variant="subtitle2" component="h2">
                      Goal: reduce risk of rupture
                    </Typography>
                    <Typography variant={'body1'}>
                      There are some behaviors that are known to increase the chance of rupture. These include smoking and high blood pressure. For certain low-risk aneurysms, this may be all that is required
                    </Typography>
                  </Grid>
                  <Button href={'#/about-rfm'} color={'primary'} size={'small'}>Learn More about Risk Reduction
                    <ChevronRight/></Button>
                </Grid>
                <Grid item={true} xs={12} sm={4} className={'norm_padding'}>
                  <img src={donothing_img} alt={'Unavailable at this time.'} href={'#/about-rfm'} className={'limit_md grid_img'}/>
                </Grid>
              </Grid>

            <Divider/>
            <Grid item={true} container={true} direction={size_class === 'large'
                  ? 'row'
                  : 'column'} xs={12} spacing={16} className={'content-block  '}>
                <Grid item={true} xs={12} sm={6}>
                  <Card >
                    <CardMedia>
                      <img src={angio_img} alt={'Unavailable at this time.'} className={'limit_md grid_img'}/>
                    </CardMedia>

                    <CardContent>

                      <Typography gutterBottom={true} variant="h6" component="h2" style={{
                          'paddingTop' : 16
                        }}>
                        For some patients: Watch and Wait
                      </Typography>
                      <Typography gutterBottom={true} variant="subtitle2" component="h2">
                        Goal: collect more data
                      </Typography>
                      <Typography component="p">
                        For some aneurysms with a low risk of rupture, it is best not to perform a risky procedure. However, it can be helpful to wait some time and then test again to see whether the anuerysm is growing - a sign that the anuerysm is more likely to rupture.
                      </Typography>
                    </CardContent>

                    <CardActions>
                      <Button href={'#/about-img'} color={'primary'} size={'small'}>Learn More about Watch and Wait
                        <ChevronRight/></Button>

                    </CardActions>
                  </Card>
                </Grid>
                <Grid item={true} xs={12} sm={6}>
                  <Card >

                    <CardMedia>
                      <img src={coiling_img} alt={'Unavailable at this time.'}  className={'limit_md grid_img'}/></CardMedia>

                    <CardContent>

                      <Typography gutterBottom={true} variant="h6" component="h2" style={{
                          'paddingTop' : 16
                        }}>
                        For some patients: Have Surgery
                      </Typography>
                      <Typography gutterBottom={true} variant="subtitle2" component="h2">
                        Goal: decrease rupture risk
                      </Typography>
                      <Typography component="p">
                        For anuerysms that are more likely to rupture, it may be worth the risk to do surgery to treat the aneurysm. The intent of these surgeries is to reduce the risk of rupture in the future.
                      </Typography>
                    </CardContent>

                    <CardActions>
                      <Button href={'#/about-img'} color={'primary'} size={'small'}>Learn More about Watch and Wait
                        <ChevronRight/></Button>

                    </CardActions>
                  </Card>
                </Grid>

              </Grid>

            <Divider/>
            <Grid id={'decisionframe'} item={true} container={true} spacing={16} xs={12} className={'content-block'} alignItems={'center'}>
              <Grid item={true} xs={12} sm={6} className='landing_text_block'>

                <Typography variant={'h5'} gutterBottom={true}>Should I do something about it?</Typography>
                <Typography variant={'body1'} paragraph={true}>Studies have been done that help doctors predict which aneurysms may cause problems. Although it might seem like a good idea to treat all aneurysms just in case, the treatments themselves can cause problems, or complications. For this reason, the risk and benefits of treatment must be balanced, and this balance will be different for every person.
                </Typography>
                <Typography variant={'body1'} paragraph={true}>
                  When you meet the doctor who will manage your aneurysm, you will discuss the risks and benefits of treating or not treating your aneurysm.
                </Typography>
              </Grid>
              {
                this.props.patient.phases !== undefined
                  ? <Grid item={true} xs={12} sm={6} container={true} justify={'center'} alignItems={'center'} direction={'column'}>
                      <Typography variant={'body1'}>
                        Percent Risk in Different Management Options
                      </Typography>
                      <Typography >
                        <b style={{
                            color: '#777777'
                          }}>Good outcome </b> <b style={{
                            color: '#be202e'
                          }}> Bad outcome</b>
                      </Typography>
                      {this.compareChart()}
                      <Grid container={true} item={true} xs={12} className={'norm_padding'} justify={'center'}>
                        {this.props.patient.ucas !== null? <Button onClick={()=>this.setGraphProb('ucas')} size={'small'} disabled={this.state.graphProb==='ucas'} color={'primary'}>See UCAS Data</Button>:null}<Button onClick={()=>this.setGraphProb('isuia')} size={'small'} disabled={this.state.graphProb==='isuia'} color={'primary'}>See ISUIA Data</Button><Button onClick={()=>this.setGraphProb('phases')} size={'small'} disabled={this.state.graphProb==='phases'} color={'primary'}>See PHASES Data</Button>
                      </Grid>

                    </Grid>

                  : null
              }
              {
                this.props.patient.ucas !== undefined
                  ?
              <Typography className={'callout'}>Monitor data is the calculated lifetime  risk of rupture  drawn from <span className={'all-caps'}>{this.state.graphProb}</span>  data and US census data on life expectancy. Endovascular and surgery outcomes are drawn from ISUIA.  These numbers are <b>estimates</b>, and you should discuss with your medical provider to better understand your individual risk.
              </Typography> : null}

              <Grid item={true} container={true} xs={12} justify={'flex-end'}>
                <Button href='#/options' color={'primary'} size={'large'} variant={'contained'}>See Comparison<ChevronRight/></Button>
              </Grid>
            </Grid>

          </Paper>
        </Grid>
        <Hidden smDown={true}>
          <Grid item={true} sm={3}>
            {this.drawer()}
          </Grid>
        </Hidden>

      </Grid>
    </div >)
  }
}

export default Decision;
