import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

export default class Header extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      menu: false,
      anchorEl: null,
      setAnchorel: null

    }
  }


  openMenu(event) {
    this.setState({
      menu: !this.state.menu,
      anchorEl: event.currentTarget
    })
  }

  closeMenu() {
    this.setState({
      menu: !this.state.menu,
      anchorEl: null,
    })
  }


  render() {
    return (<AppBar position={'static'} color={'default'}>
      <Toolbar>


        <Typography variant={'h6'} color={'primary'} className={'header-text'}>Aneurysm Patient Support Tool</Typography>

        <Button href={'#/landing'} color={'primary'} variant={'outlined'} className={'header-button'} size={'small'}>
          Home</Button>

        <Button variant={'outlined'} color={'primary'} className={'header-button'} onClick={(e) => this.openMenu(e)} size={'small'}>
          Providers
        </Button>
        <Menu id="simple-menu" anchorEl={this.state.anchorEl} open={this.state.menu} onClose={() => this.closeMenu()}>
          <MenuItem><a href={'#/data-entry'}>Risk Calculator</a></MenuItem>
          <MenuItem><a href={'#/img-slideshow'}>SlideShow</a></MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>)
  }
}
